      @refetch-data="refetchData"
    /> -->

    <!-- Filters -->
    <!-- <convocatorias-list-filters
      :role-filter.sync="roleFilter"
      :plan-filter.sync="planFilter"
      :status-filter.sync="statusFilter"
      :role-options="roleOptions"
      :status-options="statusOptions"
    /> -->

    <!-- Table Container Card -->
    <b-card
      no-body
      class="mb-0 px-2"
    >
      <div class="m-2">

        <!-- Table Top -->
        <b-row>

          <!-- Per Page -->
          <b-col
            cols="12"
            md="6"
            class="d-flex align-items-center justify-content-start mb-1 mb-md-0"
          >
            <label>Mostrar</label>
            <b-form-select
              v-model="perPage"
              :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
              :options="pageOptions"
              :clearable="false"
              class="per-page-selector d-inline-block mx-50"
            />
            <label>registros</label>
          </b-col>

          <!-- Search -->
          <b-col
            cols="12"
            md="6"
          >
            <div class="d-flex align-items-center justify-content-end">
              <b-form-input
                v-model="filter"
                size="md"
                type="search"
                class="d-inline-block mr-1"
                placeholder="Buscar..."
                debounce="400"
              />

            </div>
          </b-col>
        </b-row>

      </div>

      <b-table
        id="my-table"
        ref="refTablaBuzonDenuncias"
        class="position-relative"
        show-empty
        head-variant="light"
        hover
        small
        responsive
        :busy.sync="isBusy"
        :items="myProvider"
        :fields="tableColumns"
        :current-page="currentPage"
        :empty-text="'No hay registros para mostrar'"
        :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
        :per-page="perPage"
        :filter="filter"
        :filter-included-fields="filterOn"
        :sort-by="sortBy"
        :sort-desc="sortDesc"
        :height="myProvider.length < 5 ? '200px' : null"
      >

        <template #table-busy>
          <div class="text-center text-danger my-2">
            <b-spinner class="align-middle" />
            <strong>Cargando...</strong>
          </div>
        </template>

        <!-- Column: servicios -->
        <!-- <template #cell(servicios)="data">
          <div
            v-for="xx in data.item.serviciosx"
            :key="xx.codigo"
            class="text-nowrap"
          >
            <feather-icon
              :icon="resolveServicioIcon(xx.codigo)"
              size="15"
              class="mr-50"
              :class="`text-${resolveServicioVariant(xx.codigo)}`"
            />
            <span
              class="align-text-top text-capitalize"
              :class="`text-${resolveServicioVariant(xx.codigo)}`"
            >{{ xx.nombre }} ({{ xx.n_becas }})</span>
          </div>
        </template> -->

        <!-- Column: comunicados -->
        <template #cell(index)="data">
          {{ data.index + 1 }}
        </template>
        <template #cell(accion)="data">
          <b-button
            size="sm"
            variant="outline-danger"
            @click="cambiarComoVisto(data.item)"
          >
            <span class="text-nowrap">pasar a denuncias</span>
            <!-- <feather-icon icon="PlusSquareIcon" /> -->
          </b-button>
        </template>

        <!-- Nombre -->
        <!-- <template #cell(nombre)="data">
          <span class="text-primary"><strong>{{ data.item.nombre }}</strong></span>
        </template> -->

        <!-- Column: estado -->
        <!-- <template #cell(estado)="data">
          <b-badge
            pill
            :variant="`light-${resolveEstadoVariant(data.item.estado)}`"
            class="text-capitalize"
          >
            {{ data.item.estado }}
          </b-badge>
        </template> -->

        <!-- Incripcion -->
        <!-- <template #cell(inscripcion)="data">
          <span class="text-primary"><strong>Inicio: </strong></span>{{ new Intl.DateTimeFormat('es-PE',{month: 'long', day:'numeric', year:'numeric', hour:'numeric', minute: 'numeric'}).format(new Date(data.item.fecha_inicio)) }} <br>
          <span class="text-primary"><strong>Fin: </strong></span>{{ new Intl.DateTimeFormat('es-PE',{month: 'long', day:'numeric', year:'numeric', hour:'numeric', minute: 'numeric'}).format(new Date(data.item.fecha_fin)) }} <br>
        </template> -->

        <!-- Column: estado -->
        <!-- <template #cell(accion)="data">
          <div v-if="data.item.estado == 'borrador' || data.item.estado == 'vigente'">
            <b-button
              :variant="data.item.estado !== 'vigente' ? 'secondary' : 'info'"
              size="sm"
              block
              :disabled="data.item.estado !== 'vigente' ? true : false"
              @click="postularConvocatoria(data.item.id)"
            >Postular
              <feather-icon
                icon="ArrowRightIcon"
                size="12"
                class="align-middle text-white"
              />
            </b-button>
          </div>
          <div v-else-if="data.item.estado == 'concluido'">
            <b-button
              variant="outline-info"
              size="sm"
              block
              :href="data.item.archivo_resultado"
              :target="'_blank'"
            >Resultados
              <feather-icon
                icon="ClipboardIcon"
                size="12"
                class="align-middle"
              />
            </b-button>
          </div>
        </template> -->
      </b-table>
      <div class="mx-2 mb-2">
        <b-row>
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-start"
          >
            <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>
          </b-col>
          <!-- Pagination -->
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-end"
          >
            <b-pagination
              v-model="currentPage"
              :total-rows="totalRows"
              :per-page="perPage"
              first-number
              last-number
              class="mb-0 mt-1 mt-sm-0"
              prev-class="prev-item"
              next-class="next-item"
            >
              <template #prev-text>
                <feather-icon
                  icon="ChevronLeftIcon"
                  size="18"
                />
              </template>
              <template #next-text>
                <feather-icon
                  icon="ChevronRightIcon"
                  size="18"
                />
              </template>
            </b-pagination>
          </b-col>
        </b-row>
      </div>
    </b-card>
    <!-- <convocatoria-list-postulantes /> -->
  </div>
</template>

<script>
import {
  BCard, BRow, BCol, BFormInput, BTable, BSpinner, BFormSelect,
  BPagination, BButton,
} from 'bootstrap-vue'
// import vSelect from 'vue-select'
import { onUnmounted } from '@vue/composition-api'
import { avatarText } from '@core/utils/filter'
import store from '@/store'
import Ripple from 'vue-ripple-directive'
// import ConvocatoriasListFilters from './ConvocatoriasListFilters.vue'
// import useConvocatoriasList from './useConvocatoriasList'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import buzonStoreModule from './buzonStoreModule'
// import ComunicadosModal from '../comunicados/ComunicadosModal.vue'
// import ConvocatoriaModalAddNew from './ConvocatoriaModalAddNew.vue'

export default {
  components: {
    // ConvocatoriasListFilters,
    // UserListAddNew,
    // ConvocatoriaListPostulantes,

    BCard,
    BRow,
    BCol,
    BFormInput,
    BTable,
    BSpinner,
    // BMedia,
    // BAvatar,
    // BLink,
    // BBadge,
    // BDropdown,
    // BDropdownItem,
    BPagination,
    BButton,
    BFormSelect,

    // externs
    // ConvocatoriaModalAddNew,
    // ComunicadosModal,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      isBusy: false,
      from: 1,
      to: 1,
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, 50],
      sortBy: 'id',
      sortDesc: false,
      // sortDirection: 'desc',
      filter: '',
      filterOn: [],

    }
  },
  methods: {
    actualizarTabla() {
      this.$refs.refTablaBuzonDenuncias.refresh()
    },
    async myProvider(ctx) {
      // console.log('CTX: ', ctx)
      const promise = store.dispatch('buzon-store-module/getBuzonDenuncias', { queryParams: ctx })
      // const promise = this.$http.get('/servicio-social/beca/convocatoria', { queryParams: ctx })
      // Must return a promise that resolves to an array of items
      return promise.then(response => {
        console.log('responseBuzonDenuncias: ', response)
        const {
          data, total, from, to,
        } = response.data
        // Pluck the array of items off our axios response
        const items = data
        this.totalRows = total || 0
        // this.from = meta.pagination.count
        this.from = from || 0
        this.to = to || 0

        return items || []
      })
    },
    cambiarComoVisto(item) {
      const DATA = {
        formulario_id: item.formulario_id,
        usuario_anonimo: item.usuario_anonimo,
      }
      store.dispatch('buzon-store-module/pasarAdenuncias', DATA)
      // this.$http.get(`/servicio-social/beca/convocatoria/${idConvocatoria}`)
        .then(() => {
          this.$toast({
            component: ToastificationContent,
            position: 'top-right',
            props: {
              title: 'Registro pasado a denuncias.',
              icon: 'CheckCircleIcon',
              variant: 'success',
            },
          })
          this.actualizarTabla()
        })
    },
    nuevaConvocatoria() {
      this.$emit('nuevaConvocatoria')
    },
    editConvocatoria(idConvocatoria) {
      store.dispatch('buzon-store-module/getConvocatoria', idConvocatoria)
      // this.$http.get(`/servicio-social/beca/convocatoria/${idConvocatoria}`)
        .then(res => {
          console.log('editREspoonse: ', res)
          // this.dataConvocatoria = res.data
          this.$emit('editarConvocatoria', res.data)
        })
    },
    eliminarConvocatoria(idConvocatoria) {
      this.$swal({
        title: 'Esta seguro de eliminar la convocatoria',
        text: 'considere que esta acción es irreversible',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Eliminar convocatoria',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          store.dispatch('buzon-store-module/deleteConvocatoria', idConvocatoria)
          // this.$http.delete(`/servicio-social/beca/convocatoria/${idConvocatoria}`)
            .then(res => {
              console.log('RESPUESTA DE DELETE CONVOCATORIA: ', res)
              this.actualizarTabla()
              this.$toast({
                component: ToastificationContent,
                position: 'top-right',
                props: {
                  title: 'Convocatoria eliminada.',
                  icon: 'CheckCircleIcon',
                  variant: 'success',
                },
              })
            }).catch(error => {
              console.log(error)
            })
        }
      })
    },
    postularConvocatoria(idConvocatoria) {
      // this.$http.post(`/servicio-social/beca/convocatoria/${idConvocatoria}/pre-inscripcion`)
      store.dispatch('buzon-store-module/createPreinscripcion', idConvocatoria)
        .then(() => {
          // console.log('response postular: ', res)
          this.$router.push({ name: 'bu-convocatorias-postular', params: { id: idConvocatoria } })
        }).catch(error => {
          // console.error('Error al crear preinscripción: ', error.response)
          if (error.response.status === 409) {
            this.$swal({
              icon: 'info',
              title: '¡Información!',
              text: error.response.data.error,
              customClass: {
                confirmButton: 'btn btn-success',
              },
            })
          }
        })
    },
    comunicados(idConvocatoria) {
      // this.$emit('abrirModalComunicados', convocatoriaId)
      this.idConvocatoria = idConvocatoria
      this.$bvModal.show('modal-convocatoria-comunicados')
    },
    filtrarConvocatorias(codigo) {
      this.filtroEstado = codigo
      this.actualizarTabla()
      this.btnActivo = codigo
      if (codigo === 'concluido') {
        this.textHistorial = 'Concluidos'
      }
      if (codigo === 'cancelado') {
        this.textHistorial = 'Cancelados'
      }
      if (codigo !== 'concluido' && codigo !== 'cancelado') {
        this.textHistorial = 'Historial'
      }
    },
  },
  setup() {
    const BUZON_APP_STORE_MODULE_NAME = 'buzon-store-module'

    // Register module
    if (!store.hasModule(BUZON_APP_STORE_MODULE_NAME)) store.registerModule(BUZON_APP_STORE_MODULE_NAME, buzonStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(BUZON_APP_STORE_MODULE_NAME)) store.unregisterModule(BUZON_APP_STORE_MODULE_NAME)
    })

    // const isAddNewUserSidebarActive = ref(false)

    const roleOptions = [
      { label: 'Comedor', value: 'Comedor' },
      { label: 'Residencia', value: 'Residencia' },
      { label: 'Bolsa de trabajo', value: 'Bolsa de trabajo' },
    ]

    const tableColumns = [

      { key: 'index', label: 'N°' },
      // { key: 'usuario_anonimo', label: 'usuario anonimo', sortable: true },
      // { key: 'formulario_id', label: 'formulario_id', sortable: true },
      { key: 'nombres', label: 'nombres', sortable: true },
      { key: 'dni', label: 'dni', sortable: true },
      { key: 'email', label: 'email', sortable: true },
      { key: 'accion', label: 'acción', sortable: true },
    ]

    const resolveEstadoVariant = status => {
      if (status === 'borrador') return 'secondary'
      if (status === 'vigente') return 'success'
      if (status === 'en curso') return 'warning'
      if (status === 'concluido') return 'danger'
      if (status === 'cancelado') return 'danger'
      return 'primary'
    }
    const resolveServicioVariant = value => {
      if (value === 'BECA-COMEDOR') return 'primary'
      if (value === 'BECA-RESIDENCIA') return 'success'
      if (value === 'BECA-BOLSA') return 'info'
      return 'primary'
    }
    const resolveServicioIcon = value => {
      if (value === 'BECA-COMEDOR') return 'CoffeeIcon'
      if (value === 'BECA-RESIDENCIA') return 'HomeIcon'
      if (value === 'BECA-BOLSA') return 'BriefcaseIcon'

      return 'UserIcon'
    }
    // const {
    //   fetchUsers,
    //   tableColumns,
    //   perPage,
    //   currentPage,
    //   totalUsers,
    //   dataMeta,
    //   perPageOptions,
    //   searchQuery,
    //   sortBy,
    //   isSortDirDesc,
    //   refUserListTable,
    //   refetchData,

    //   // UI
    //   resolveServicioVariant,
    //   resolveServicioIcon,
    //   resolveEstadoVariant,

    //   // Extra Filters
    //   roleFilter,
    //   planFilter,
    //   statusFilter,
    // } = useConvocatoriasList()

    return {
      buzonStoreModule,
      // isAddNewUserSidebarActive,

      // fetchUsers,
      tableColumns,
      // perPage,
      // currentPage,
      // totalUsers,
      // dataMeta,
      // perPageOptions,
      // searchQuery,
      // sortBy,
      // isSortDirDesc,
      // refUserListTable,
      // refetchData,

      avatarText,

      resolveServicioVariant,
      resolveServicioIcon,
      resolveEstadoVariant,

      roleOptions,

      // roleFilter,
      // planFilter,
      // statusFilter,
    }
  },
}
</script>

<style lang="scss" scoped>
.per-page-selector {
  width: 90px;
}
</style>

